.contact__container {
  grid-template-columns: 5fr 7fr;
  display: grid;
}
.contact__card {
  padding-left: 130px;
  margin-bottom: 40px;
  position: relative;
}

.contact__card-icon,
.contact__form-input {
  border-color: 2px solid var(--border-color);
  background-color: var(--container-color);
}

.contact__card-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  box-shadow: var(--shadow);
  font-size: var(--normal-font-size);
  display: grid;
  place-items: center;
}

.contact__card-icon::after {
  content: "";
  width: 40px;
  height: 2px;
  background-color: var(--border-color);
  position: absolute;
  left: 100%;
  top: 50%;
  margin: -1px;
}

.contact__card-title {
  font-size: var(--h2-font-size);
  font-family: var(--second-font);
  padding-block: 6px;
}

.contact__form-group {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 30px;
}

.contact__form-div {
  display: grid;
  row-gap: 10px;
  margin-bottom: 30px;
}

.contact__form-tag {
  margin-left: 30px;
  font-size: var(--smaller-font-size);
  font-weight: 700;
}

.contact__form-tag b {
  color: var(--primary-color);
}

.contact__form-input {
  color: var(--title-color);
  height: 45px;
  border-radius: 30px;
  padding-inline: 30px;
}

.contact__form-area .contact__form-input {
  height: 140px;
  padding-block: 20px;
  resize: none;
  border-color: var(--secondary-color);
}

.contact__submit {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  column-gap: 30px;
}


.contact_reply{
  display: none;
   font-size: var(--big-font-size);
    font-weight: var(--font-semi-bold);
    text-align: center;
    color: rgb(2, 110, 199);
    padding-bottom: 30px;
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {



}

/* For medium devices */
@media screen and (max-width: 768px) {
   .contact__container{
    grid-template-columns:auto;
   }

   .contact__form-group {
    grid-template-columns: repeat(1, 1fr);
    column-gap: 15px;
}
}

@media screen and (min-width: 800px) and (max-width: 1334px) {

   .contact__container{
    grid-template-columns:1fr 2fr;;
   }

   .contact__form-group {
    grid-template-columns: repeat(1, 4fr);
    column-gap: 10px;
}
}



