.resume {
  background-image: var(--second-gradient);
}

.resume__container {
  grid-template-columns: repeat(2, 1fr);
}

.resume__heading {
  text-align: center;
  font-size: var(--h3-font-size);
  padding-bottom: 30px;
}

.resume__heading,
.resume__item {
  border-bottom: 2px solid var(--border-color);
}

.resume__item:not(:first-child) {
  border-right: 2px solid var(--border-color);
}

.resume__item {
  position: relative;
}

.resume__header {
  padding: 32px 30px;
}

.resume__subtitle {
  font-size: var(--normal-font-size);
  cursor: pointer;
}

.resume__icon {
  background-color: var(--secondary-color);
  border: 2px solid var(--border-color);
  font-size: var(--h3-font-size);
  box-shadow: var(--shadow);
  width: 38px;
  height: 38px;
  border-radius: 50%;
  line-height: 1.9rem;
  text-align: center;
  cursor: pointer;
  position: absolute;

  right: -24px;
  bottom: -20px;
  z-index: 10;
  transition: all 1.2s var(--transition);
}

.resume__content {
  padding-inline: 30px 50px;
  max-height: 0;
  overflow: hidden;
  transition: all 1s ease;
}

.show-content {
  max-height: 500px;
}

.resume__date-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.resume__title {
  font-family: var(--second-font);
  font-size: var(--h3-font-size);
}

.resume__date {
  color: var(--title-color);
  font-size: var(--smaller-font-size);
  font-weight: 700;
}

.resume__description {
  margin-block: 20px 30px;
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
  .resume__container {
    grid-template-columns: repeat(1, 1fr);
  }

  .resume__icon {
    right: -19px;
  }
}

/* For medium devices */
@media screen and (max-width: 768px) {
  /* .swiper{
        margin-left: 50px;
    }*/
}

@media screen and (min-width: 800px) and (max-width: 1334px) {
  .resume__container {
    margin: 30px;
  }
}
